.social {
    padding: 30px 0;
    border-bottom: 1px solid #e5e5e5;

    @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    
       padding: 0;
        border-bottom: none;
        
     
       
       
        
    }
    
}

.social__list {
    margin: 0;
    padding: 0;
    width: 141px;
    
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.social__item {
    list-style: none;
}

.social-link--instagram-img {
    background-image: url('../img/mobile/social-link--instagram-img.png');
    width: 22px;
    height: 20px;
    background-repeat: no-repeat;
}

.social-link--facebook-img {
    background-image: url('../img/mobile/social-link--facebook-img.png');
    width: 15px;
    height: 28px;
    background-repeat: no-repeat;
}

.social-link--twitter-img {
    background-image: url('../img/mobile/social-link--twitter-img.png');
    background-repeat: no-repeat;
    width: 23px;
    height: 20px;
    
}


@media (min-width: $desktop-width) {
    
    .social {

    border-bottom: none;
    }
 
   
   
    
}