.button {
    display: block;
    width: 86%;
    border: 2px solid $dark-grey;
    margin: 0;
    padding: 0;
    padding: 18px 0;
    margin: 0 auto;

    color: $dark-grey;
    text-align: center;
    text-transform: uppercase;


    transform: translateX(-1px);

    @media (min-width: $tablet-width) and (max-width: $desktop-width) {
        width: 254px;
    }

    @media (min-width: $desktop-width) {
        width: 254px;
    }
}

.button:hover {
    background-color: $dark-grey;
    color: #ffffff;
}